form {
  display: flex;
  /* background-color: #fff; */

  height: 3rem;
  padding: 0;
  align-items: center;
  justify-content: center;
  width: 34vw;
  box-sizing: border-box;
  margin: 0;
  margin-top: 2rem;
}
@media screen and (max-width: 768px) {
  form {
    width: 90vw;
  }
}

form > input {
  height: 100%;
  width: 100%;
  border: 0;
  margin: auto;

  outline: 0;
  padding-left: 0.5rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
form > button {
  height: 110%;
  width: 12rem;
  color: #fff;
  align-self: center;
  margin: auto;
  border: 0;
  background-color: #324e9b;
  border-top-right-radius: 5px;
  /* border-bottom-right-radius: 5px; */
  outline: 0;
  padding: 0 1rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
