.login {
  /* border: 1px solid red; */
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  height: fit-content;
  position: absolute;
  height: fit-content;
  background-color: teal;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 100%);
}
.login input {
  height: 3rem;

  width: 90%;
  background-color: rgb(193, 231, 231);
  border-radius: unset;
  color: black !important;
}

.login button {
  width: 91.5%;
  height: 3rem;
  text-align: center;
}
.login > div {
  color: #fff !important
  ;
  text-align: center;
  font-size: 40px !important;
}

@media screen and (max-width: 768px) {
  .login {
    width: 100%;
  }
}
