.welcome {
  display: flex;
  flex-flow: row wrap;
  width: 100vw;
  background-color: teal;
  justify-content: space-around;
  text-align: start;
  height: 5rem;
  align-items: center;
  margin-bottom: 2rem;
}
.welcome span {
  height: 3rem;
  text-align: center;
}

.welcome button {
  height: 3rem;
  border-radius: 10px;
  width: 8rem;
  margin-left: 4%;
  padding: 0.5rem 1.5rem !important;
  font-size: large;
  font-weight: bold;
  color: rgb(80, 80, 172);
  outline: 0;
  border: 0;
}
